@use '../index.scss'as *;

$carousel-width                :80;
$navigation-button-width-height:5;
$navigation-button-margin      :1;

$images-in-view-portion : 4;

@function calculation-view-portion-width() {
    //(carsousel-width)   minus         (2 nav button width)      minus    (2 buttons left and right margins combined)
    @return $carousel-width - (2 * $navigation-button-width-height) - (4 * $navigation-button-margin);
}

$viewable-image-portion : calculation-view-portion-width();



@function calculate-image-space() {
    @return (($viewable-image-portion / $images-in-view-portion));
}

:export {
    imagesInViewPortion     : $images-in-view-portion;
    viewableImagePortionInVW: $viewable-image-portion;
    imageWidthInVW          : calculate-image-space();
}

.carousel {
    width      : #{$carousel-width}vw;
    display    : flex;
    margin-top : 10vh;
    align-items: center;

    button {
        cursor       : pointer;
        width        : #{$navigation-button-width-height}vw;
        height       : #{$navigation-button-width-height}vw;
        font-size    : #{$navigation-button-width-height}vw;
        color        : #404040;
        border-radius: 10px;
        background   : white;
        border       : 1px solid silver;
        @include vertically-and-horizontally-center;
        margin    : #{$navigation-button-margin}vw;
        transition: .3s ease-in-out;

        &[disabled] {
            cursor : default;
            opacity: 0.1;
        }
    }

    .images-container {
        display        : flex;
        width          : #{$viewable-image-portion}vw;
        overflow-x     : scroll;
        scroll-behavior: smooth;
        @include hide-scroll-bar;

        .image-container {

            min-width  : calculate-image-space() * 1vw;
            height     : calculate-image-space() * 1vw;
            padding    : 0.8rem;
            position   : relative;
            padding-top: 2vw;
            text-align : left;

            .title {
                position  : absolute;
                margin-top: -25px;
            }

            .image-wrapper {
                width  : 100%;
                height : 100%;
                padding: 0.5rem;
                @include vertically-and-horizontally-center;
                border       : 1px solid silver;
                border-radius: 6px;


                img {
                    max-width : 100%;
                    max-height: 100%;
                }
            }
        }

    }

}